.card {
  display: grid;
  grid-template-rows: repeat(2, 80px) 1fr;
  justify-items: center;
  text-align: center;
  max-width: $cardWidth;
  padding: 10px;
  justify-self: center;

  @media (max-width: $desktopBreakpoint) {
    max-width: $cardBigWidth;
  }

  &__text {
    font-size: 14px;
    line-height: 150%;
  }
}
