.form {
  display: flex;
  flex-direction: column;
  width: 440px;

  @media (max-width: $tabletBreakpoint) {
    width: 100%;
  }

  &__input {
    margin-bottom: 20px;
  }

  &__textarea {
    height: 130px;
    margin-bottom: 30px;
    resize: none;
  }

  &__btn {
    border: none;
    line-height: 0;
  }
}
