.header {
  text-transform: uppercase;
  font-size: 14px;
  overflow: hidden;

  @media (max-width: $tabletBreakpoint) {
    position: sticky;
    top: 0;
    background: $c-bg;
    z-index: 2;
  }

  &__container {
    display: grid;
    grid-template-columns: 1fr repeat(3, $btnWidth);
    align-items: center;
    margin-top: 13px;
    margin-bottom: 0;

    @media (max-width: $tabletBreakpoint) {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__logotype {
    width: $logotypeWidth;
  }

  &__switcher {
    display: block;
    width: 54px;

    @media (max-width: $tabletBreakpoint) {
      display: none;
    }
  }

  &__lang {
    color: $c-basicExtraLight;

    &--is-active {
      color: $c-basicDark;
    }
  }

  &__btn {
    @media (max-width: $tabletBreakpoint) {
      display: none;
    }
  }

  &__label {
    display: none;
    z-index: 2;
    width: $iconSize;
    height: $iconSize;
    cursor: pointer;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(/images/burger.svg);

    @media (max-width: $tabletBreakpoint) {
      display: block;
      justify-self: flex-end;
    }
  }

  &__toggler {
    display: none;
    z-index: 2;

    &:checked + .header__label {
      background-image: url(/images/cross.svg);
      z-index: 2;
    }

    &:checked ~ .mobile-menu {
      transform: translate(-100vw);
    }
  }
}
